#root{
  overflow-x: hidden;
}
:root{
  --box-shadow:5px 5px 10px 10px rgb(28, 145, 223);
  --bg-color:rgb(40,47,97);
}
body{
  background:linear-gradient(TO RIGHT,#191714,#2234AE);
  color: white;
}
.nav-link:hover
{
  border-bottom: 1px solid rgb(28, 145, 223);
color: rgb(255, 193, 7);

}
.t{
  color:white;
}
.home{
  height:80vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home .left{
  width: 620px;
  text-align: center;
  border-radius:10px;
  padding: 30px;
  box-shadow: var(--box-shadow);
}
.home .right .image{
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.home .right .image img{
  height: 270px;
  width: 270px;
  border-radius: 100%;
  border: 5px solid 212, 140, 6;
  animation-name: superman;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}
@keyframes superman{
  0%{
    border-color:rgb(28, 145, 223);
    transform: translate(0,0px);
    box-shadow: 10px 10px 100px 50px rgb(28, 145, 223);
  }
  25%{
    border-color:rgb(28, 145, 223);
    box-shadow: 10px 10px 100px 50px rgb(28, 145, 223);
  }
  50%{
    border-color: rgb(28, 145, 223);
    transform: translate(0,10px);
    box-shadow: 10px 10px 100px 50px rgb(28, 145, 223);
  }
  75%{
    border-color: rgb(28, 145, 223);
    transform: translate(0,0px);
    box-shadow: 10px 10px 100px 50px rgb(28, 145, 223);
  }
  100%{
    border-color: rgb(28, 145, 223);
    transform: translate(0,-10px);
    box-shadow: 10px 10px 100px 50px rgb(28, 145, 223);
  }
}
    .ed{
      width: 900px;
    }
    .ed-item{
      display: flex;
      justify-content: center;
      align-items: center;
      background-color:rgb(223, 122, 28);
      margin: 20px;
      margin-bottom: 20px;
      padding: 30px;
      width: 640px;
      border-radius: 10px;
      /* border:1px solid orange; */
      box-shadow:5px 5px 10px 10px rgb(245, 139, 40);
    }
    .skill{
      width: 900px;
    }
    .skill .items{
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      margin: auto;
    }
    .skill .items .item{
      width: calc(33.33%);
      margin:20px;
      background-color:rgb(0, 0, 90);
      border: 2px solid rgb(28, 145, 223);
      text-align: center;
      padding: 20px;
      border-radius: 10px;
    }
    .skill .items .item img{
      width: 70px;
      height: 70px;
      border-radius: 100%;
    } 
    .contact-icon{
      height: 30vh;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .contact-icon .items:hover{
      background:linear-gradient( 45deg,#2234AE,purple);
      height: 80px;
      width: 80px;
    }
    .contact-icon .items{
      background:linear-gradient(TO RIGHT,#2234AE,#191714);
      color: white;
      margin: 10px;
      height: 70px;
      width: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 100%;
      border: 1px solid yellow;

    }
    .contact-icon .items .icons{
      font-size: 2rem;

    }
    @media (max-width:750px) {
      .home{
        display: block;
        margin-top: 3rem;
        height: 70vh;
      }
      .home .left{
        width: auto;
        padding: 0;
        margin: auto;
        margin-bottom: 2rem;
      }
      .skill{
        width: auto;
        margin-top: 1rem;
      }
      .skill .items{
        display: block;
        
      }
      .skill .items .item{
        width: auto;
        
      }
    }
    @media (max-width:1200px) {
      .project .row .card{
        max-width: 16rem;
      }
    }
    @media (max-width:980px) {
      .project .row .card{
        width: auto;
        max-width: 21rem;
      }
      .home .left{
        width: 320px;
        padding: 20px;
      }
    }
    @media (max-width:570px) {
      .project .row .card{
        margin: auto;
      }
      .contact-icon .items .icons{
        font-size: 1.5rem;
        
      }
      .contact-icon .items{
        width: 60px;
        height: 60px;
       
      }
    }
   
    
    